.image {
    background: #fff;
    border-radius: 2px;
    height: 150px;
    margin: 1rem;
    position: relative;
    width: 150px;
    -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
            box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
    
  }

  .image > .img-container {
    padding-top: 20%;
    height: 100%;
    overflow: hidden;
    text-align: center;
    background:#6CADDC;
  }
  .image > .img-container > img {
    width: 60%
  }


